'use client';

import { useState } from 'react';
import { Button } from '@/components/ui/Button';
import { Input } from '@/components/ui/Input';
import { Textarea } from '@/components/ui/Textarea';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/Card';

export default function AppointmentPage() {
  const [loading, setLoading] = useState(false);
  const [success, setSuccess] = useState(false);

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setLoading(true);
    
    // Simulate API call
    setTimeout(() => {
      setLoading(false);
      setSuccess(true);
    }, 1500);

    // TODO: Connect to actual API route
    // const formData = new FormData(e.currentTarget);
    // await fetch('/api/appointment', { method: 'POST', body: formData });
  };

  if (success) {
    return (
      <div className="flex min-h-screen items-center justify-center bg-secondary/20 px-4">
        <Card className="w-full max-w-md text-center">
          <CardHeader>
            <CardTitle className="text-2xl text-primary">Request Sent!</CardTitle>
            <CardDescription>
              Thank you for contacting us. Our team will review your appointment request and get back to you shortly.
            </CardDescription>
          </CardHeader>
          <CardContent>
             <Button onClick={() => setSuccess(false)}>Book Another</Button>
          </CardContent>
        </Card>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-slate-50 py-12 px-4">
      <div className="container mx-auto max-w-3xl">
        <h1 className="text-3xl font-bold text-center mb-8 text-primary">Book an Appointment</h1>
        <Card>
          <CardHeader>
             <CardTitle>Patient Information</CardTitle>
             <CardDescription>Please fill in the details below to schedule an appointment.</CardDescription>
          </CardHeader>
          <CardContent>
            <form onSubmit={handleSubmit} className="space-y-6">
              <div className="grid md:grid-cols-2 gap-4">
                <div className="space-y-2">
                  <label htmlFor="name" className="text-sm font-medium">Patient Name</label>
                  <Input id="name" name="name" required placeholder="John Doe" />
                </div>
                <div className="space-y-2">
                  <label htmlFor="age" className="text-sm font-medium">Age</label>
                  <Input id="age" name="age" type="number" required placeholder="65" />
                </div>
              </div>

              <div className="grid md:grid-cols-2 gap-4">
                <div className="space-y-2">
                  <label htmlFor="phone" className="text-sm font-medium">Phone Number</label>
                  <Input id="phone" name="phone" type="tel" required placeholder="+91 9876543210" />
                </div>
                <div className="space-y-2">
                  <label htmlFor="service" className="text-sm font-medium">Type of Service</label>
                  <select 
                    id="service" 
                    name="serviceType" 
                    className="flex h-10 w-full items-center justify-between rounded-md border border-input bg-background px-3 py-2 text-sm placeholder:text-muted-foreground focus:outline-none focus:ring-2 focus:ring-ring focus:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50"
                    required
                  >
                    <option value="">Select a service</option>
                    <option value="In-Patient Care">In-Patient Care</option>
                    <option value="Home Care">Home Care</option>
                    <option value="Dialysis">Free Dialysis</option>
                    <option value="Pain Management">Pain Management</option>
                    <option value="Physiotherapy">Physiotherapy</option>
                  </select>
                </div>
              </div>

              <div className="space-y-2">
                <label htmlFor="address" className="text-sm font-medium">Address</label>
                <Textarea id="address" name="address" required placeholder="Full residential address" />
              </div>

              <div className="space-y-2">
                <label htmlFor="date" className="text-sm font-medium">Preferred Date</label>
                 <Input id="date" name="preferredDate" type="date" required />
              </div>

              <div className="space-y-2">
                <label htmlFor="message" className="text-sm font-medium">Additional Message (Optional)</label>
                <Textarea id="message" name="message" placeholder="Any specific condition or requirement..." />
              </div>

              <Button type="submit" className="w-full" disabled={loading}>
                {loading ? 'Submitting...' : 'Submit Request'}
              </Button>
            </form>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
